.apod {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    min-height: 600px;

    &__information {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;
        margin-right: 30px;

        &__title {
            margin-bottom: 20px;
        }

        &__description {
            p {
                font-size: 1.3rem;
            }
        }

        &__buttons-actions {
            display: flex;
            justify-content: space-between; 
            margin: 60px 0;
        
            button {
                padding: 10px;
                background: transparent;
                border: 0;
                outline: none;
                cursor: pointer;
            }
        }
    }

    &__content {
        width: 50%;
        max-height: 550px;

        &__img {
            width: 100%;
            border-radius: 20px;
            max-height: 100%;
            object-fit: cover;
        }

        &__copyright {
            text-align: right;
            padding: 10px 0;
        }
    }
}

